.special-nav-button {
    background-color: #f7941E;
    color: #fff;;
    border: none;
    padding: 1px 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    text-decoration: none;
    font-weight: 500;
    font-family: 'Open Sans Condensed', sans-serif;
}


@media (max-width: 1310px) {
    .special-wrap {
        justify-content: flex-end;
    }

}